import Request from './request'

const test = new Request()

test.setConfig((config) => { /*  */
  config.baseUrl = 'https://cscbnew.kelinteng.com/api/'



  config.header = {
    ...config.header,
    'content-type':'application/json;charset=UTF-8',
    'content-type':'application/x-www-form-urlencoded',
  }
  return config
})

test.interceptor.request((config, cancel) => { /*  */
  config.header = {
    ...config.header
  }
  /*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
  return config
})

test.interceptor.response((response) => { /*  */
  return response
}, (response) => { // 
  return response
})

const http = new Request()

http.setConfig((config) => { /*  */
  config.baseUrl = 'http://www.bbb.cn' /*  */
  return config
})

http.validateStatus = (response) => {
  return response.statusCode === 200
}

http.interceptor.request((config, cancel) => { /*  */
  config.header = {
    ...config.header,
    a: 1
  }
  /*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
  return config
})

http.interceptor.response((response) => { /*  */
  return response
}, (response) => { // 
  return response
})

export {
  http,
  test
}
